:root {
  --rad: .7rem;
  --dur: .3s;
  --color-dark: #2f2f2f;
  --color-light: #fff;
  --color-brand: #57bd84;
  --font-fam: "Lato", sans-serif;
  --height: 5rem;
  --btn-width: 6rem;
  --bez: cubic-bezier(0, 0, .43, 1.49);
}

body {
  min-height: 100vh;
  background: #dbdbdb;
  margin: 0;
  padding-bottom: 20px;
}

html {
  box-sizing: border-box;
  height: 100%;
  font-size: 10px;
}

*, :before, :after {
  box-sizing: inherit;
}

.search_container {
  height: 100px;
  background: var(--color-dark);
  margin: 0 auto 20px;
  padding-top: 5px;
}

form {
  width: 30rem;
  background: var(--color-brand);
  border-radius: var(--rad);
  margin: 20px auto;
  position: relative;
}

input, .submit-btn {
  height: var(--height);
  font-family: var(--font-fam);
  color: var(--color-dark);
  border: 0;
  font-size: 1.8rem;
}

input[type="search"] {
  width: 100%;
  background: var(--color-light);
  border-radius: var(--rad);
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  transition: all var(--dur) var(--bez);
  z-index: 1;
  outline: 0;
  padding: 0 1.6rem;
  transition-property: width, border-radius;
  position: relative;
}

input[type="search"]::-webkit-search-cancel-button {
  cursor: pointer;
}

.submit-btn {
  width: var(--btn-width);
  background: var(--color-brand);
  border-radius: 0 var(--rad) var(--rad) 0;
  cursor: pointer;
  font-weight: bold;
  display: none;
  position: absolute;
  top: 0;
  right: 0;
}

input:not(:placeholder-shown) {
  border-radius: var(--rad) 0 0 var(--rad);
  width: calc(100% - var(--btn-width) );
}

input:not(:placeholder-shown) + .submit-btn {
  display: block;
}

label {
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  width: 1px;
  border: 0;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.load-more-btn {
  height: 30px;
  color: #fff;
  border-radius: var(--rad) var(--rad) var(--rad) var(--rad);
  font-family: var(--font-fam);
  cursor: pointer;
  background-color: #db7093;
  border: none;
  margin: 100px auto 50px;
  display: block;
}

.photo-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: var(--rad) var(--rad) var(--rad) var(--rad);
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 320px;
  grid-auto-columns: 320px;
  gap: 60px 40px;
  padding: 0 20px;
  display: grid;
}

.info {
  justify-content: center;
  gap: 2rem;
  display: flex;
}

.info-item {
  text-align: center;
  font-size: 12px;
}

@media screen and (max-width: 1109px) {
  .gallery {
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 300px;
    grid-auto-columns: 300px;
  }
}

@media screen and (max-width: 1024px) {
  .gallery {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 200px;
    grid-auto-columns: 200px;
  }
}

@media screen and (max-width: 560px) {
  .gallery {
    grid-template-columns: repeat(1, 1fr);
  }
}

